export default {
    
    props: ['domainStats'],
    
    data() {
        return {
            domain_name: "",
            price_renting: "",
            price_buyout: "",
            email: "",

            submitSuccess: false,
            submitError: false,
            submitText: 'Saved!',
            suggest: false,
        }
    },
    methods: {
        async submitForm() {
            await this.$axios.$post('/open', {
                'name': this.domain_name,
                'price_renting': this.price_renting,
                'price_buyout': this.price_buyout,
                'email': this.email,
            })
            .then((response) => {
                // console.log(response)
                this.domain_name = ""
                this.price_renting = ""
                this.price_buyout = ""
                this.email = ""

                this.submitSuccess = true;
                this.submitText = 'Saved!';
                setTimeout(() => this.submitSuccess = false, 4000)

                this.$router.push('/domains-up-for-rent/add/success');
            }, (error) => {
                // console.log(error.response.data.message)
                this.submitError = true;
                this.submitText = error.response.data.message;
                setTimeout(() => {
                    this.submitError = false, 
                    this.submitText = ''
                }, 4000)
            });
        },
        calculateBuyout() {
            this.price_buyout = this.price_renting*12*5*3
            this.suggest = true
        }
    },
}
